import { queue, ga } from "@music/initializer";

class GaService {
  private channel = queue.create('page-view');

  constructor() {
    this.channel.start();
  }

  public pageView(name: string) {
    return this.channel.add({
      label: 'Page View',
      tag: "page-view",
      handler: 'GAWorker',
      args: ga.gaPageView({ name }),
    });
  }
}

export default new GaService;



