import * as React from 'react';
import { IMix, MainMixType, ISeries } from '../../interfaces';
import { Loader, Button } from 'semantic-ui-react';
import PlayButtonHoc from '../../hocs/PlayButtonHoc';
import QuickPlayHoc from '@music/hocs/QuickPlayHoc';

interface IPlayButtonProps {
  setPlayerState: (mix: IMix) => any;
  isLoading: boolean;
  isSame: boolean;
  mix: IMix;
  current: MainMixType;
  series: ISeries;
}

function PlayButton({setPlayerState, isLoading, isSame, mix, series}: IPlayButtonProps) {
  return (
    <>
    {isLoading && <Loader inline active size='mini' />}
    {!isLoading &&
      <Button
        circular
        size="big"
        icon={(isSame ? 'pause' : 'play')}
        onClick={() => setPlayerState(mix)}
      />
    }
    </>
  )
}

export default QuickPlayHoc(PlayButtonHoc(PlayButton));



