import * as React from 'react';

export default function MixDetailHoc(WrappedComponent: any) {
  return class extends React.Component<any, any> {
    constructor(props: any) {
      super(props);
      this.state = {};
    }

    public render() {
      return (
        <WrappedComponent {...this.props} {...this.state} />
      );
    }
  };
}



