import * as React from 'react';
import { connect } from 'react-redux';
import { Segment, Header, Container } from 'semantic-ui-react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { has } from '@music/utils';
import { prepareMix } from '@music/actions/mix';
import SidebarComponent from '../../layouts/Sidebar';
import MixDetailHoc from '@music/hocs/mix/DetailHoc';
import EntityCollection from '@music/extensions/entity-collection';
import TrackListBody from '@music/components/stateless/TrackListBody';
import { IState, IKeyValueObject, ISeries, IMix, ITrackCue, MainMixType } from '@music/interfaces';
import { setMainMix } from '@music/actions/player';
import { IQuickPlayHoc } from '@music/hocs/QuickPlayHoc';
import PlayButton from '@music/components/stateless/PlayButtonVar2';
import gaService from '@music/_services/gaService';

type MixDetailFullProps = IMixDetailProps & RouteComponentProps & IQuickPlayHoc;

interface IMixDetailProps {
  player: MainMixType;
  series: EntityCollection;
  container: IKeyValueObject[];
  currentTrack: ITrackCue|undefined;
  prepareMix: (mix: IMix, addToContainer: boolean, cb: (mix: IMix) => any) => any;
  setMainMix: (mix: MainMixType) => any;
}

interface IMixDetailState {
  currentSeries: ISeries|null|undefined;
  mix: IMix
}

class MixDetail extends React.Component<MixDetailFullProps, IMixDetailState> {
  public state: Readonly<IMixDetailState> = {
    currentSeries: null,
    mix: {} as IMix
  };

  constructor(props: MixDetailFullProps) {
    super(props);
  }

  get container(): EntityCollection {
    return new EntityCollection(this.props.container);
  }

  get mix(): IMix|null {
    return this.state.mix;
  }

  public componentDidMount() {
    const { slug } = this.props.match.params as IKeyValueObject;
    const currentSeries = this.props.series.find(slug, 'slug') as ISeries;
    this.setState({ currentSeries }, () => gaService.pageView('Mix Detail'));
  }

  public componentDidUpdate() {
    const { count } = this.props.match.params as IKeyValueObject;
    const series = this.state.currentSeries || {} as IMix;
    const mix = this.container.where({seriesId: series.id, count: parseInt(count)}).first() as IMix;

    if (mix && !has('cues.0.track', this.state.mix)) {
      this.props.prepareMix({...mix}, true, (prepared) => {
        this.setState({mix: prepared});
      });
    }
  }

  public render() {
    return !this.state.currentSeries  ? null : (
      <>
        <SidebarComponent>
          <Segment style={{height: '500px'}} className="wide-cover">
            {(!!this.mix && this.mix.count) &&
              <>
                <br />
                <Header as="h1">{this.state.currentSeries.name} #{this.mix.count}</Header>
                <Header as="h3">{this.mix.description}</Header>
                <PlayButton
                  mix={this.mix}
                  {...this.props}
                  play={this.props.play}
                  pause={this.props.pause}
                  current={this.props.player}
                  series={this.props.series.first()}
                />
              </>
            }
          </Segment>
          <Container>
            {(!!this.mix && this.mix.cues) &&
              <TrackListBody
                cues={this.mix.cues}
                isPlaying={this.props.player.playing}
                currentTrack={this.props.currentTrack}
              />
            }
          </Container>
        </SidebarComponent>
      </>
    );
  }
}

const mapDispatchToProps = (dispatch: CallableFunction) => {
  return {
    setMainMix: (mix: MainMixType) => dispatch(setMainMix({...mix})),
    prepareMix: (mix: IMix, addToContainer: boolean, cb: (mix: IMix) => any) => dispatch(prepareMix(mix, addToContainer, cb)),
  }
};

const mapStateToProps = (state: IState) => {
  return {
    player: state.player,
    container: [...state.mixContainer.data],
    series: new EntityCollection(state.series.data),
    currentTrack: Object.keys(state.currentTrack).length > 0 ? {...state.currentTrack} : undefined,
  }
};

const MixDetailPage = withRouter(connect(mapStateToProps, mapDispatchToProps)(MixDetailHoc(MixDetail)) as any);

export default MixDetailPage;



